  <script>

import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { onMounted } from 'vue';
import { ScrollToPlugin } from "gsap/ScrollToPlugin";


gsap.registerPlugin(ScrollTrigger,ScrollToPlugin);

  export default {
    name: "AppMain",
    setup() {
        onMounted(() => {
        const introText = gsap.timeline();
        introText
            .fromTo('main canvas',{opacity:0, y: "-100%", duration: 1, delay: 1}, { opacity: 1, y: "0" })
            .fromTo('.scroll',{opacity:0, duration: 0.5 }, { opacity: 1})
            .fromTo('.m_copy img',{opacity:0, x: -30, duration: 0.5,}, { opacity: 1, x: 0 })
            .fromTo('.m_copy span',{opacity:0, duration:0.5, y:-30}, { opacity: 1, y: 0 })
            .fromTo('.svg-txt path',{opacity:0}, {opacity: 1, duration:0.5, strokeDashoffset:0, strokeDasharray:85})
            .fromTo('.svg-txt path',{fill:"none"}, {fill:"#fff"})
            .fromTo('.m_copy em',{opacity:0, duration:0.5, y:-30}, { opacity: 1, y: 0 })
            .fromTo('.m_copy .btn-area',{opacity:0, duration:0.2, y:10}, { opacity: 1, y: 0 })
            ;
            gsap.to(".scroll span", {
                scrollTrigger: {
                trigger: "#chapter2",
                start: "top bottom",
                scrub: 6,
                },
                rotate: 180, duration: 10,
                });
            gsap.to(".scroll", {
                scrollTrigger: {
                trigger: "#chapter2",
                start: "top bottom",
                scrub: 2,
                },
                y: "120%",
            });
            gsap.to("#waveCanvas", {
                scrollTrigger: {
                trigger: "#chapter1",
                start: "top 120%",
                scrub: 0,
                },
                y: "20%", duration: 0.1,
            });
        });

    },
    data() {
    return {
      waves: [
        {
          points: [
            { x: -600, amplitude: 10, yPercentage: 59 },
            { x: 0, amplitude: 10, yPercentage: 44 },
            { x: 400, amplitude: 10, yPercentage: 29 },
            { x: 800, amplitude: 10, yPercentage: 39 },
            { x: 1200, amplitude: 10, yPercentage: 26 },
            { x: 1900, amplitude: 10, yPercentage: 54 },
            { x: 3000, amplitude: 10, yPercentage: 19 },
            { x: 4000, amplitude: 10, yPercentage: 99 },
            { x: window.innerWidth, amplitude: 0, yPercentage: 0 }
          ],
          color: '#7f6aeb',
          alpha: 1,
          offset: 0,
          speed: 0.2, 
          frequency: 0.3,
        },
        {
          points: [
            { x: -600, amplitude: 10, yPercentage: 57 },
            { x: 0, amplitude: 10, yPercentage: 42 },
            { x: 400, amplitude: 10, yPercentage: 27 },
            { x: 800, amplitude: 10, yPercentage: 37 },
            { x: 1200, amplitude: 10, yPercentage: 24 },
            { x: 1900, amplitude: 10, yPercentage: 52 },
            { x: 3000, amplitude: 10, yPercentage: 17 },
            { x: 4000, amplitude: 10, yPercentage: 97 },
            { x: window.innerWidth, amplitude: 0, yPercentage: 0 }
          ],
          color: '#7f6aeb',
          alpha: 0.2,
          offset: 0,
          speed: 0.09, 
          frequency: 0.5,
        },
      ],
      ctx: null
    };
  },
  methods: {
    drawWave(wave, phase) {
      const canvas = this.$refs.waveCanvas;
      if (!canvas) return; // canvas가 null인 경우 안전하게 반환

      const ctx = this.ctx;
      ctx.save(); // 현재 상태 저장
      ctx.translate(canvas.width / 2, canvas.height / 2); // 중심으로 이동
      ctx.rotate(Math.PI); // 180도 회전
      ctx.translate(-canvas.width / 2, -canvas.height / 2); // 원래 위치로 이동

      ctx.strokeStyle = wave.color;
      ctx.fillStyle = wave.color;
      ctx.globalAlpha = wave.alpha;
      ctx.beginPath();
      ctx.moveTo(0, (wave.points[0].yPercentage / 100) * window.innerHeight);

      wave.points.forEach((point, i) => {
        if (i < wave.points.length - 1) {
          let startX = point.x;
          let endX = wave.points[i + 1].x;
          let startY = (point.yPercentage / 100) * window.innerHeight + point.amplitude * Math.sin(phase * wave.frequency + i + wave.offset);
          let endY = (wave.points[i + 1].yPercentage / 100) * window.innerHeight + wave.points[i + 1].amplitude * Math.sin(phase * wave.frequency + i + 1 + wave.offset);
          let controlX1 = (2 * startX + endX) / 3;
          let controlY1 = startY;
          let controlX2 = (startX + 2 * endX) / 3;
          let controlY2 = endY;
          ctx.bezierCurveTo(controlX1, controlY1, controlX2, controlY2, endX, endY);
        }
      });

      ctx.lineTo(canvas.width, canvas.height);
      ctx.lineTo(0, canvas.height);
      ctx.closePath();
      ctx.fill();
      ctx.stroke();
      ctx.globalAlpha = 1.0;
      ctx.restore(); // 회전 전 상태로 복원
    },
    animate() {
      const canvas = this.$refs.waveCanvas;
      if (!canvas) return; // canvas가 null인 경우 안전하게 반환

      this.ctx.clearRect(0, 0, canvas.width, canvas.height);

      for (let wave of this.waves) {
        this.drawWave(wave, wave.phase);
        wave.phase += wave.speed;
      }

      requestAnimationFrame(this.animate);
    },
    resizeCanvas() {
      const canvas = this.$refs.waveCanvas;
      if (!canvas) return; // canvas가 null인 경우 안전하게 반환

      canvas.width = window.innerWidth;
      canvas.height = window.innerHeight;

      for (let wave of this.waves) {
        wave.points[wave.points.length - 1].x = canvas.width;
      }
    },

  },
  mounted() {
    const canvas = this.$refs.waveCanvas;
    if (!canvas) return; // canvas가 null인 경우 안전하게 반환

    this.ctx = canvas.getContext('2d');
    this.resizeCanvas();

    for (let wave of this.waves) {
      wave.phase = 0;
    }

    this.animate();

    window.addEventListener('resize', this.resizeCanvas);

  },
  beforeUnmount() {
    window.removeEventListener('resize', this.resizeCanvas);
  }
};
  </script>
  
